.home {
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;
}

.home__container {
  position: relative;
  background-color: #eaeaea;
  padding-bottom: 10px;
}

.home__image {
  width: 100%;
  z-index: -1;
  margin-bottom: -150px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home__row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 100px;
  justify-content: center;
}
